body {
  background-color: #e7eaed;
}

.App {
  max-width: 600px;
  margin: 30px auto 0;
  font-family: "Montserrat Alternates";

}

.sections {
  box-shadow: 1px 1px 10px 2px rgba(0, 0, 0, 0.2);
  padding: 15px;
  border-radius: 5px;
  margin: 30px auto;
  background-color: #fff;
}

.title {
  font-size: 3rem;
  text-align: center;
  color: #112D4E;
}

.slideContainer {
  max-width: 100%;
  min-width: 250px;
  margin: 20px auto 20px;
  text-align: center;
}

.slider {
  background: linear-gradient(to right, #415771 0%, #415771 25%, #c2daf1 25%, #c2daf1 100%);
  border: 2px solid #fff;
  border-radius: 8px;
  height: 10px;
  width: 80%;
  outline: none;
  transition: background 450ms ease-in;
  accent-color: #F9F2ED;
  -webkit-appearance: none;
}


input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
}

input[type=range]:focus {
  outline: none;
}

input[type=range]::-ms-track {
  width: 100%;
  cursor: pointer;
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  background: #112D4E;
  cursor: pointer;
  box-shadow: 1px 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.form-control {
  font-family: system-ui, sans-serif;
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.1;
  display: grid;
  grid-template-columns: 1em auto;
  gap: 0.5em;
  margin-top: 15px;
  color: #112D4E;
}

input[type="checkbox"] {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
  cursor: pointer;
  background-color: #fff;
  margin: 0;
  font: inherit;
  color: #112D4E;
  width: 1.8rem;
  height: 1.8rem;
  border: 0.15em solid #112D4E;
  border-radius: 0.15em;
  transform: translateY(-0.075em);
  display: grid;
  place-content: center;
}

input[type="checkbox"]::before {
  content: "";
  width: 0.65em;
  height: 0.65em;
  transform: scale(0);
  transition: 120ms transform ease-in-out;
  box-shadow: inset 1em 1em var(--form-control-color);
  background-color: #3F72AF;
  transform-origin: bottom left;
  clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
}

input[type="checkbox"]:checked::before {
  transform: scale(1);
}

.inputContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-bottom: 30px;
}

.first>h1 {
  color: #112D4E;
  margin-right: 10px;
  display: inline-block;
}

.first>input {
  color: #3F72AF;
  border-radius: 5px;
  border-color: #112D4E;
  background-color: #F9F7F7;
}

.second>h1 {
  display: inline-block;
}

.manualInput {
  font-weight: 600;
  padding: 7px 0 7px 3px;
  font-size: 1.5rem;
}

.password {
  box-sizing: border-box;
  font-weight: 600;
  border: 2px solid rgba(0, 0, 0, 0.6);
  width: 100%;
  padding: 8px;
  border-radius: 10px;
  line-height: 2rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 600px;
  font-size: 1.5rem;
  font-family: sans-serif;
  letter-spacing: 1px;
  color: #3F72AF;
  margin-bottom: 10px;
}

.buttons {
  width: 150px;
  margin: 25px auto 0;
  text-align: center;
}

.buttons button {
  cursor: pointer;
  color: #3F72AF;
  border: solid 2px #112D4E;
  background-color: white;
  font-size: 2.5rem;
  padding: 5px;
  transition: ease-in 0.25s;
  border-radius: 8px;
  margin: 0 10px;
}


#copy.copied {
  color: white;
  background-color: #2EB086;
  border: solid 2px #2EB086;
}

.buttons button:disabled {
  color: #545d5a;
  border: solid 2px #545d5a;
}

.buttons button:disabled:hover {
  color: #545d5a;
  cursor: not-allowed;
  background-color: white;
}

.copiedLabel {
  margin-bottom: 0;
  transition: ease-in 0.3s;
  color: #2EB086;
}

.buttons button:hover {
  background-color: #112D4E;
  color: white;
}

.fa-circle-check {
  display: none;
}

.strength {
  text-align: center;
  font-size: 30px;
}

.fa-arrows-rotate.rotate {
  animation: rotation 0.4s linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width:400px) {
  .form-control {
    font-size: 1.3rem;
  }

  .first {
    margin-right: 5px;
    font-size: 13px;
  }

  .buttons button {
    font-size: 2rem;
  }
}